<template>
  <div class="home">
    <h1>Remove names you don't like</h1>
    <h2>Click on a name to remove it from the list</h2>
  </div>
  <div class="names">
    <div class="name" v-for="name in names" :key="name" :class="rating[names.indexOf(name)] > 50 ? 'top' : 'bottom'"
      @click="removeName(name)">
      {{ name }}
    </div>
  </div>
  <div class="names-removed">
    <h3>Names Removed: {{ namesRemoved }}</h3>
  </div>
</template>

<script>

export default {
  name: 'HomeView',
  data() {
    return {
      names: [],
      rating: [],
      vovels: ['a', 'e', 'i', 'o', 'u'],
      consonants: ['b', 'c', 'd', 'f', 'g', 'h', 'j', 'k', 'l', 'm',
        'n', 'p', 'q', 'r', 's', 't', 'v', 'w', 'x', 'y', 'z'],
      option: ['vcvcv', 'cvcvc', 'ccvcv', 'cvvcv', 'cvcvcv', 'vcvcvc', 'cvccv', 'vccvc', 'cvccvc', 'vcvcc'],
      namesRemoved: 0
    }
  },
  methods: {
    removeName(name) {
      const index = this.names.indexOf(name)
      this.names[index] = this.generateName()
      this.rating[index] = -1;
      for (let i = 0; i < this.rating.length; i++) {
        this.rating[i]++
      }
      this.namesRemoved++
    },
    generateName() {
      const option = this.option[Math.floor(Math.random() * this.option.length)].split('')
      for (let i = 0; i < option.length; i++) {
        if (option[i] === 'v') {
          option[i] = this.vovels[Math.floor(Math.random() * this.vovels.length)]
        } else {
          option[i] = this.consonants[Math.floor(Math.random() * this.consonants.length)]
        }
      }
      return option.join('')
    }
  }, mounted() {
    //repeate 10 times
    for (let i = 0; i < 10; i++) {
      this.names.push(this.generateName())
      this.rating.push(0)
    }
  }
}
</script>

<style scoped>
.names {
  display: flex;
  width: 55vw;
  margin: auto;
  flex-wrap: wrap;
  justify-content: space-around;
}

.name {
  margin: 10px;
  background-color: #333;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  width: 25vw;
  height: 5vw;
  font-size: 4vw;
  text-align: center;
}

.name:hover {
  background-color: #444;
}

.name:active {
  background-color: #555;
}

h1 {
  font-size: xx-large;
}

h2 {
  font-size: x-large;
}

.top {
  box-shadow: #00ff00 0px 0px 10px 0px;
}
</style>

